"use client";

import React, { useState } from "react";
import Button from "@/components/UI/Button";
import Input from "@/components/UI/Input";

const LoginForm: React.FC = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  return (
    <form className="bg-background-2 p-8 rounded-lg shadow-md w-full max-w-md space-y-6">
      <h1 className="text-primary-900 text-2xl font-bold">¡Bienvenido de nuevo!</h1>
      <p className="text-neutral-600 text-sm">Ingresa tus credenciales para continuar.</p>

      <Input
        label="Usuario"
        placeholder="Ingresa tu correo o tu usuario"
        icon="/assets/email.svg"
        type="email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />

      <Input
        label="Contraseña"
        placeholder="Ingresa tu contraseña"
        icon="/assets/password.svg"
        type="password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />

      <div className="flex items-center justify-between text-sm text-neutral-600">
        <label className="flex items-center space-x-2">
          <input type="checkbox" className="rounded text-primary focus:ring-2" />
          <span>Recordar por 30 días</span>
        </label>
        <a href="/reset-password" className="text-primary hover:underline">
          ¿Olvidaste tu contraseña?
        </a>
      </div>

      <Button text="Iniciar sesión" type="submit" />
    </form>
  );
};

export default LoginForm;
