"use client";

import React from "react";
import LoginForm from "./LoginForm";
import ThemeSwitcher from "../dashboard/ThemeSwitcher";

const LoginPage: React.FC = () => {
  return (
    <div
      className="relative flex items-center justify-center h-screen bg-cover bg-center"
      style={{
        backgroundImage: "url('/assets/background.jpg')", // Imagen de fondo
      }}
    >
      {/* Contenedor principal */}
      <div className="flex items-center justify-between max-w-6xl mx-auto w-full px-4 md:px-8">
        {/* Imagen del astronauta */}
        <div className="hidden lg:block w-1/2">
          <img
            src="/assets/astronauta.png"
            alt="Astronauta"
            className="max-w-full h-auto"
          />
        </div>

        {/* Contenedor del formulario */}
        <div className="w-full lg:w-1/2 bg-white dark:bg-gray-900 p-8 rounded-xl shadow-lg">
          {/* Toggle de tema */}
          <div className="flex justify-end mb-6">
            <ThemeSwitcher />
          </div>

          {/* Formulario de inicio de sesión */}
          <LoginForm />
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
